import { ActionTypesUser, Activity, Project } from "../../common/types/user";

export type Action =
  | { type: ActionTypesUser.SET_ACTIVITIES; payload: Activity[] }
  | { type: ActionTypesUser.SET_FROM_DATE; payload: Date }
  | { type: ActionTypesUser.SET_TO_DATE; payload: Date }
  | { type: ActionTypesUser.SET_PROJECTS; payload: Project[] }
  | {
      type: ActionTypesUser.SET_CHAT_AND_USER_ID;
      payload: { chatId: number; userId: number };
    }
  | { type: ActionTypesUser.SET_NAME; payload: string };

const setActivities = (activities: Activity[]): Action => {
  return { type: ActionTypesUser.SET_ACTIVITIES, payload: activities };
};

const setProjects = (projects: Project[]): Action => {
  return { type: ActionTypesUser.SET_PROJECTS, payload: projects };
};

const setFromDate = (date: Date): Action => {
  return { type: ActionTypesUser.SET_FROM_DATE, payload: date };
};

const setToDate = (date: Date): Action => {
  return { type: ActionTypesUser.SET_TO_DATE, payload: date };
};

const setChatAndUserId = (chatId: number, userId: number): Action => {
  return {
    type: ActionTypesUser.SET_CHAT_AND_USER_ID,
    payload: { chatId, userId },
  };
};

const setName = (payload: string): Action => {
  console.log(payload);
  return { type: ActionTypesUser.SET_NAME, payload };
};

export {
  setActivities,
  setProjects,
  setFromDate,
  setToDate,
  setChatAndUserId,
  setName,
};
