import React, { useContext, useEffect, useReducer } from "react";
import CalendarContext from "./calendar-context";
import calendarContextReducer from "./reducers";
import { useQuery } from "react-query";
import moment from "moment";
import ServicesContext from "../services-context";
import Spinner from "../../components/spinner";
import ErrorIndicator from "../../components/error-indicator";
import { setEvents } from "./actions";
import { UserContext } from "../user-context";
import { Event } from "react-big-calendar";
import { SpinnerWrapper } from "../../common/styles/spinner-wrapper";

const CalendarContextProvider: React.FC = ({ children }) => {
  const [state, dispatch] = useReducer(calendarContextReducer, {
    date: new Date(),
    events: [],
    loadingEvents: false,
  });

  const { calendarService } = useContext(ServicesContext);
  const mondayCurrent = moment(new Date()).startOf("week").toDate().getTime();
  const saturdayCurrent = moment(new Date()).endOf("week").toDate().getTime();

  const {
    state: { userId, chatId },
  } = useContext(UserContext);

  const { data, isFetching, error } = useQuery<Event[]>("calendar-data", () =>
    calendarService.getEventData(userId, chatId, mondayCurrent, saturdayCurrent)
  );

  useEffect(() => {
    if (data) {
      dispatch(setEvents(data));
    }
  }, [data]);

  if (isFetching) {
    return (
      <SpinnerWrapper>
        <Spinner />
      </SpinnerWrapper>
    );
  }

  if (error) {
    return <ErrorIndicator />;
  }

  if (!data) {
    return <p>You have not used this bot yet..</p>;
  }

  const value = { state, dispatch };

  return (
    <CalendarContext.Provider value={value}>
      {children}
    </CalendarContext.Provider>
  );
};

export default CalendarContextProvider;
