import React, {FC, useContext} from 'react';
import ServicesContext from "../../contexts/services-context";
import {useQuery} from "react-query";
import Spinner from "../spinner";
import ErrorIndicator from "../error-indicator";
import {ResponsiveCalendar} from '@nivo/calendar'
import {CalendarData} from "../../common/interfaces/user-service";
import GraphInnerWrapper from "../../common/styles/graph-inner-wrapper";

interface Props {
  data: CalendarData[],
  loading: boolean
}

const CalendarGraph: FC<Props> = ({data, loading}) => {
  if (loading) return <Spinner/>
  return (
    <GraphInnerWrapper>
      <ResponsiveCalendar
        data={data}
        from="2015-01-01"
        to="2015-12-29"
        emptyColor="#eeeeee"
        colors={['#61cdbb', '#97e3d5', '#e8c1a0', '#00ff0e']}
        minValue={2}
        margin={{top: 40, right: 40, bottom: 40, left: 40}}
        yearSpacing={60}
        yearLegendOffset={15}
        monthBorderWidth={0}
        monthBorderColor="#ffffff"
        dayBorderWidth={2}
        dayBorderColor="#ffffff"
        legends={[
          {
            anchor: 'bottom-right',
            direction: 'row',
            translateY: 36,
            itemCount: 4,
            itemWidth: 42,
            itemHeight: 36,
            itemsSpacing: 14,
            itemDirection: 'right-to-left'
          }
        ]}
      />
    </GraphInnerWrapper>
  )
};

export default CalendarGraph;