import React, {FC} from 'react';
import CalendarGraphContainer from "../../containers/calendar-graph-container";
import DayActivitiesGraph from "../../containers/day-activities-graph-container";
import ActivitiesPieGraphContainer from "../../containers/activities-pie-graph-container";
import DailySleepGraphContainer from "../../containers/daily-sleep-graph-container";
import AverageSleepGraphContainer from "../../containers/average-sleep-graph-container";
import ProjectByActivityGraphContainer from "../../containers/project-by-activity-graph-container";
import ActivitiesBarGraphContainer from "../../containers/activities-bar-graph-container";
import {CalendarWrapper, DoubleRowWrapper, UserPageWrapper} from "./user-page.styles";

const UserPage: FC = () => {
  return (
    <UserPageWrapper>
      <CalendarWrapper>
        <CalendarGraphContainer/>
      </CalendarWrapper>
      <DoubleRowWrapper>
        <ActivitiesPieGraphContainer/>
        <DayActivitiesGraph/>
      </DoubleRowWrapper>
      <DoubleRowWrapper>
        <DailySleepGraphContainer/>
        <AverageSleepGraphContainer/>
      </DoubleRowWrapper>
      <DoubleRowWrapper>
        <ProjectByActivityGraphContainer/>
        <ActivitiesBarGraphContainer/>
      </DoubleRowWrapper>


    </UserPageWrapper>
  );
};

export default UserPage;