import React, { useEffect, useState } from "react";
import { Wrapper } from "./time.styles";
import moment from "moment";

interface TimeProps {
  time: Date;
  setTime: (time: Date) => void;
}

const Time: React.FC<TimeProps> = ({ time, setTime }) => {
  const [value, setValue] = useState("");

  useEffect(() => {
    setValue(moment(time).format("HH:mm"));
  }, [time]);

  const handlerChange = ({
    target: { value },
  }: React.ChangeEvent<HTMLInputElement>) => {
    const hoursAndMinutes = value.split(":");
    time.setHours(+hoursAndMinutes[0]);
    time.setMinutes(+hoursAndMinutes[1]);
    setTime(time);
    setValue(value);
  };

  return (
    <Wrapper>
      <input type="time" onChange={handlerChange} value={value} required />
    </Wrapper>
  );
};

export default Time;
