import React, { useContext } from "react";
import { Wrapper } from "./header.styles";
import { FaTelegramPlane } from "react-icons/fa";
import { IconContext } from "react-icons";
import { UserContext } from "../../contexts/user-context";
import { Link } from "react-router-dom";

const Header = () => {
  const {
    state: { chatId, userId, name },
  } = useContext(UserContext);

  return (
    <>
      <Wrapper>
        <div className="logo-with-title">
          <img
            src={`${process.env.REACT_APP_BACK_URL}user/photo?user_id=${userId}`}
            alt="img"
          />
          <span>
            <Link to={`/user/${chatId}/${userId}`}>{name}</Link>
          </span>
        </div>
        <div className="telegram-logo">
          <IconContext.Provider value={{ color: "#fff", size: "50px" }}>
            <a href={`https://t.me/${process.env.REACT_APP_BOT_USERNAME}`}>
              <FaTelegramPlane />
            </a>
          </IconContext.Provider>
        </div>
      </Wrapper>
    </>
  );
};

export default Header;
