import React from "react";
import { Switch, Route } from "react-router-dom";
import CalendarPage from "../../pages/calendar-page";
import UserPage from "../../pages/user-page";
import ChatPage from "../../pages/chat-page";
import SubHeader from "../../components/sub-header";
import { CalendarContextProvider } from "../../contexts/calendar-context";
import TelegramLogin from "../telegram-login-container";
import ParamsContainer from "../params-container";

const RouterContainer = () => {
  return (
    <Switch>
      <Route exact path="/user/:chat_id/:user_id">
        <ParamsContainer>
          <SubHeader />
          <UserPage />
        </ParamsContainer>
      </Route>
      <Route exact path="/calendar/:chat_id/:user_id">
        <ParamsContainer>
          <TelegramLogin>
            <CalendarContextProvider>
              <CalendarPage />
            </CalendarContextProvider>
          </TelegramLogin>
        </ParamsContainer>
      </Route>
      <Route exact path="/chat/:chat_id">
        <ParamsContainer>
          <ChatPage />
        </ParamsContainer>
      </Route>
    </Switch>
  );
};

export default RouterContainer;
