import React, { useContext, useState, useEffect } from "react";
import TelegramLoginButton from "react-telegram-login";
import { Wrapper } from "./telegram-login.styles";
import ServicesContext from "../../contexts/services-context";
import Spinner from "../../components/spinner";
import { UserContext } from "../../contexts/user-context";
import { SpinnerWrapper } from "../../common/styles/spinner-wrapper";

const TelegramLogin: React.FC = ({ children }) => {
  const [userLogin, setUserLogin] = useState(false);
  const [isFetching, setIsFetching] = useState(true);
  const { calendarService } = useContext(ServicesContext);

  const {
    state: { userId },
  } = useContext(UserContext);

  useEffect(() => {
    if (!userId) {
      return;
    }

    calendarService
      .userIsAuth(userId)
      .then(() => setUserLogin(true))
      .catch((e) => console.log(e))
      .finally(() => setIsFetching(false));
  }, [userId]);

  const handleTelegramResponse = async (response: any) => {
    setIsFetching(true);
    await calendarService.authUserForCalendar(response);
    setUserLogin(true);
    setIsFetching(false);
  };

  if (isFetching) {
    return (
      <SpinnerWrapper>
        <Spinner />
      </SpinnerWrapper>
    );
  }

  if (!userLogin) {
    return (
      <Wrapper>
        <span>
          Для использования этой функции необходимо войти через телеграм
        </span>
        <TelegramLoginButton
          dataOnauth={handleTelegramResponse}
          botName={process.env.REACT_APP_BOT_USERNAME}
        />
      </Wrapper>
    );
  }

  return <>{children}</>;
};

export default TelegramLogin;
