import React, {useCallback, useContext, useEffect, useState} from 'react';
import DayActivitiesGraph from "../../components/day-activities-graph";
import ServicesContext from "../../contexts/services-context";
import {useQuery} from "react-query";
import {DayActivitiesData} from "../../common/interfaces/user-service";
import Spinner from "../../components/spinner";
import ErrorIndicator from "../../components/error-indicator";
import {Fill} from "../../common/types/graphs";
import {GraphOuterWrapper} from "../../common/styles/graph.styles";

const DayActivitiesGraphContainer = () => {
  const {userService} = useContext(ServicesContext);
  const {isFetching, data, error} = useQuery<DayActivitiesData[]>('barData', userService.getDayActivitiesData);
  const [fills, setFills] = useState<Fill[]>([]);

  const getKeys = useCallback(
    (): string[] => {
      const keys: string[] = [];
      if (data) {
        data.map(i => {
          for (const [key, value] of Object.entries(i)) {
            if (
              key !== 'activity' &&
              keys.findIndex(i => i === key) === -1 &&
              !key.match(new RegExp('Color', 'ig'))) {
              keys.push(key);
            }
          }
        });
      }
      else {
        return []
      }
      return keys;
    }, [data]);

  useEffect(() => {
    const barStyleMap = ['lines', 'dots', 'squares', 'nothing'];

    setFills((fills) => {
      return getKeys().map(i => {
        return {
          id: barStyleMap[Math.floor(Math.random() * 4)],
          match: {
            id: i
          }
        }
      });
    });
  }, [data]);


  if (error) {
    return (<ErrorIndicator/>);
  }

  if (!data) {
    return (<p>You have not used this bot yet..</p>);
  }


  return (
    <GraphOuterWrapper>
      <DayActivitiesGraph loading={isFetching} data={data} indexBy={'activity'} keys={getKeys()} fills={fills}/>
    </GraphOuterWrapper>
  )
}

export default DayActivitiesGraphContainer;