import React, {useState} from "react";
import {Wrapper} from "./dropdown.style";
import {Activity} from "../../common/types/user";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faChevronLeft} from "@fortawesome/free-solid-svg-icons";
import ReactDropdown from 'react-dropdown';
import {Themes} from './dropdown.style';

interface DropdownProps {
  data: Activity[];
  value: number | string;
  onSelect: (value: number) => void;
  placeholder?: string;
  theme?: Themes
}

const Dropdown: React.FC<DropdownProps> =
  ({
     data,
     onSelect,
     value,
     placeholder = "",
     theme
   }) => {
  const [open, setOpen] = useState<boolean>(false);
    return (
      <Wrapper open={open} themeColor={theme}>
        <ReactDropdown
          options={data.map(a => {
            return {value: a.id.toString(), label: a.name}
          })}
          onChange={(options) => onSelect(+options.value)}
          value={value.toString()}
          className="select"
          controlClassName="select-control"
          placeholderClassName="select-placeholder"
          menuClassName="select-menu"
          arrowClosed={<FontAwesomeIcon icon={faChevronLeft}/>}
          arrowOpen={<FontAwesomeIcon icon={faChevronLeft}/>}
          arrowClassName="select-arrow"
          placeholder={placeholder}
        />
        <svg className="sprites">
          <symbol id="select-arrow-down" viewBox="0 0 10 6">
            <polyline points="1 1 5 5 9 1"/>
          </symbol>
        </svg>
      </Wrapper>
    )
  };

export default Dropdown;
