import { ActionTypesCalendar, CalendarData } from "../../common/types/calendar";
import { Action } from "./actions";

const calendarContextReducer = (state: CalendarData, action: Action) => {
  switch (action.type) {
    case ActionTypesCalendar.ADD:
      return { ...state, events: [...state.events, action.payload] };

    case ActionTypesCalendar.UPDATE:
      return {
        ...state,
        events: state.events.map((item) => {
          if (
            item.resource.activityRecord ===
            action.payload.resource.activityRecord
          ) {
            return action.payload;
          }
          return item;
        }),
      };

    case ActionTypesCalendar.DELETE:
      return {
        ...state,
        events: state.events.filter(
          (item) =>
            item.resource.activityRecord !==
            action.payload.resource.activityRecord
        ),
      };

    case ActionTypesCalendar.SET_EVENTS:
      return {
        ...state,
        events: action.payload,
      };

    case ActionTypesCalendar.SET_DATE:
      return {
        ...state,
        date: action.payload,
      };

    case ActionTypesCalendar.SET_LOADING_EVENTS:
      return { ...state, loadingEvents: action.payload };

    default:
      return state;
  }
};

export default calendarContextReducer;
