import styled from "styled-components";

export const Wrapper = styled.div`
  z-index: 1000;
  position: fixed;
  height: 65px;
  width: 100%;
  background: linear-gradient(
    90deg,
    rgba(230, 69, 88, 1) 34%,
    rgba(220, 90, 51, 0.8382703423166141) 72%
  );
  margin-bottom: 25px;
  color: #fff;
  display: flex;
  justify-content: space-between;

  a {
    color: #ffffff;
    text-decoration: none;
  }
  a:visited {
    color: #ffffff;
  }
  
  .telegram-logo{
    a {
      margin-right: 15px;
    }  
  }
  

  .logo-with-title {
    display: flex;
    align-items: center;
    height: 100%;

    img {
      height: 80%;
      margin-left: 15px;
      border-radius: 50px;
    }

    span {
      margin-left: 10px;
      font-size: 20px;
    }
  }

  .telegram-logo {
    display: flex;
    align-items: center;

    a {
      margin-top: 8px;
    }
  }
`;
