import React, {useCallback, useContext, useEffect, useState} from 'react';
import ActivitiesBarGraph from "../../components/activities-bar-graph";
import ServicesContext from "../../contexts/services-context";
import {useQuery} from "react-query";
import {ActivitiesBarData} from "../../common/interfaces/user-service";
import {Fill} from "../../common/types/graphs";
import Spinner from "../../components/spinner";
import ErrorIndicator from "../../components/error-indicator";
import {GraphOuterWrapper} from "../../common/styles/graph.styles";


const ActivitiesBarGraphContainer = () => {
  const {userService} = useContext(ServicesContext);
  const {isFetching, data, error} = useQuery<ActivitiesBarData[]>('ActivitiesBarData', userService.getActivitiesBarData);
  const [fills, setFills] = useState<Fill[]>([]);
  const [filter, setFilter] = useState<string[]>([]);

  const getKeys = useCallback(
    (): string[] => {
      const keys: string[] = [];
      if (data) {
        data.map(i => {
          for (const [key, value] of Object.entries(i)) {
            if (
              key !== 'date' &&
              keys.findIndex(i => i === key) === -1 &&
              !key.match(new RegExp('Color', 'ig'))) {
              keys.push(key);
            }
          }
        });
      }
      else {
        return []
      }
      return keys;
    }, [data, filter]);

  useEffect(() => {
    const barStyleMap = ['lines', 'dots', 'squares', 'nothing'];

    setFills((fills) => {
      return getKeys().map(i => {
        return {
          id: barStyleMap[Math.floor(Math.random() * 4)],
          match: {
            id: i
          }
        }
      });
    });
  }, [data, filter]);

  if (error) {
    return (<ErrorIndicator/>);
  }

  if (!data) {
    return (<p>You have not used this bot yet..</p>);
  }

  return (
    <GraphOuterWrapper>
      <ActivitiesBarGraph loading={isFetching} data={data} fills={fills} indexBy={'date'} keys={getKeys()} />
    </GraphOuterWrapper>
  )
}

export default ActivitiesBarGraphContainer;