import React, { useContext, useState } from "react";
import { Button, Wrapper } from "./sub-header.styles";
import DatePicker from "react-date-picker";
import { Link } from "react-router-dom";
import { UserContext } from "../../contexts/user-context";

const SubHeader = () => {
  const [from, setFrom] = useState<Date | Date[]>(new Date());
  const [to, setTo] = useState<Date | Date[]>(new Date());

  const {
    state: { chatId, userId },
  } = useContext(UserContext);

  return (
    <Wrapper>
      <div className="sub-header-content">
        <div className="from-to">
          <DatePicker
            className="from"
            onChange={setFrom}
            value={from}
          />
          <DatePicker
            className="to"
            onChange={setTo}
            value={to}
          />
        </div>
        <div className="calendar">
          <Link to={`/calendar/${chatId}/${userId}`}>
            <Button>Calendar</Button>
          </Link>
        </div>
      </div>
    </Wrapper>
  );
};

export default SubHeader;
