import React, { useContext, useEffect, useState } from "react";
import { Event } from "react-big-calendar";
import ServicesContext from "../../contexts/services-context";
import { Project } from "../../common/types/user";
import AddEventsModal from "../../components/add-events-modal";
import { EventResource } from "../../common/types/calendar";
import { CalendarContext } from "../../contexts/calendar-context";
import { UserContext } from "../../contexts/user-context";
import {
  addEvent,
  deleteEvent,
  updateEvents,
} from "../../contexts/calendar-context";

interface PropsCalendarModalContainer {
  isOpen: boolean;
  onClose: () => void;
  from: Date;
  to: Date;
  setFrom: (from: Date) => void;
  setTo: (to: Date) => void;
  selectedEvent: Event | null;
}

const CalendarModalContainer: React.FC<PropsCalendarModalContainer> = ({
  isOpen,
  onClose,
  from,
  to,
  setFrom,
  setTo,
  selectedEvent,
}) => {
  const { dispatch } = useContext(CalendarContext);
  const {
    state: { activities, projects, chatId, userId },
  } = useContext(UserContext);
  const { calendarService } = useContext(ServicesContext);

  const [activityId, setActivityId] = useState<number | "">("");
  const [projectId, setProjectId] = useState<number | "">("");
  const [errorDelete, setErrorDelete] = useState(false);

  const [projectsCurrentActivity, setProjectsCurrentActivity] = useState<
    Project[]
  >([]);

  const [isCorrectData, setIsCorrectData] = useState(true);
  const [isSending, setSending] = useState(false);

  useEffect(() => {
    if (!selectedEvent) {
      return;
    }

    setActivityId(selectedEvent.resource.activity);

    if (selectedEvent.resource?.project) {
      setProjectId(selectedEvent.resource.project);
    }
    updateProjects(selectedEvent.resource.activity);
  }, [selectedEvent]);

  const updateProjects = (id: number) => {
    if (data) {
      const currentProjects = projects!.filter(
        (item) => item.activityId === id
      );
      setProjectsCurrentActivity(currentProjects);
    }
  };

  const handlerSetActivity = (id: number) => {
    setActivityId(id);
    setProjectId("");
    updateProjects(id);
  };

  const createActivity = async (title: string) => {
    let resource: EventResource;

    if (typeof activityId !== "number") {
      return setIsCorrectData(false);
    }
    const fromMilisecond = from.getTime();
    const toMilisecond = to.getTime();

    if (typeof projectId === "number") {
      resource = await calendarService.createActivity(
        activityId,
        userId,
        chatId,
        fromMilisecond,
        toMilisecond,
        projectId
      );
    } else {
      resource = await calendarService.createActivity(
        activityId,
        userId,
        chatId,
        fromMilisecond,
        toMilisecond
      );
    }
    dispatch(addEvent({ start: from, end: to, title, resource }));
    setIsCorrectData(true);
    onClose();
    document.body.style.overflow = "unset";
  };

  const updateActivity = async (title: string) => {
    if (typeof activityId !== "number") {
      return setIsCorrectData(false);
    }

    if (typeof projectId === "number") {
      await calendarService.updateActivity(
        userId,
        from.getTime(),
        to.getTime(),
        selectedEvent!.resource.activityRecord,
        activityId,
        projectId
      );
    }

    dispatch(
      updateEvents({
        start: from,
        end: to,
        resource: {
          activityRecord: selectedEvent!.resource.activityRecord,
          activity: activityId,
          project: projectId,
        },
        title,
      })
    );

    await calendarService.updateActivity(
      userId,
      from.getTime(),
      to.getTime(),
      selectedEvent!.resource.activityRecord,
      activityId
    );

    onClose();
    document.body.style.overflow = "unset";
  };

  const handlerSubmit = async (e: React.SyntheticEvent) => {
    e.preventDefault();
    try {
      if (from.getTime() > to.getTime()) {
        setIsCorrectData(false);
        return;
      }
      setSending(true);
      const title = activities!.find((item) => item.id === activityId)!.name;

      if (selectedEvent) {
        await updateActivity(title);
        setActivityId("");
        setProjectId("");
        return;
      }
      await createActivity(title);
      setActivityId("");
      setProjectId("");
    } catch (e) {
      setIsCorrectData(false);
    } finally {
      setSending(false);
    }
  };

  const handlerDelete = async () => {
    try {
      setSending(true);
      await calendarService.deleteActivity(
        userId,
        selectedEvent!.resource.activityRecord
      );

      dispatch(deleteEvent(selectedEvent!));
    } catch (e) {
      setErrorDelete(true);
    } finally {
      setSending(false);
      onClose();
    }
  };

  const handlerAfterClose = () => {
    setIsCorrectData(true);
    setActivityId("");
    setProjectId("");
    setProjectsCurrentActivity([]);
    document.body.style.overflow = "unset";
  };

  const handlerAfterOpen = () => {
    document.body.style.overflow = "hidden";
  };

  const data = { activities, projects };

  return (
    <AddEventsModal
      isSending={isSending}
      isOpen={isOpen}
      data={data}
      handlerAfterClose={handlerAfterClose}
      handlerAfterOpen={handlerAfterOpen}
      onClose={onClose}
      handlerSubmit={handlerSubmit}
      activityId={activityId}
      projectId={projectId}
      handlerSetActivity={handlerSetActivity}
      projectsCurrentActivity={projectsCurrentActivity}
      setProjectId={setProjectId}
      from={from}
      to={to}
      setFrom={setFrom}
      setTo={setTo}
      isCorrectData={isCorrectData}
      errorDelete={errorDelete}
      isSelectedEvent={!!selectedEvent}
      onDelete={handlerDelete}
    />
  );
};

export default CalendarModalContainer;
