import { EventResource, IDataFromBack } from "../common/types/calendar";
import instance from "./instance";
import { Event } from "react-big-calendar";

export default class CalendarService {
  async getEventData(
    user_id: number,
    chat_id: number,
    from_date: number,
    to_date: number
  ): Promise<Event[]> {
    const res = await instance.post<IDataFromBack[]>("/calendar/activities", {
      user_id,
      chat_id,
      from_date,
      to_date,
    });

    return res.data.map((item) => {
      return {
        title: item.activity_name,
        start: new Date(item.start_time),
        end: new Date(item.end_time),
        resource: {
          activityRecord: item.id,
          activity: item.activity_type,
          project: item.subactivity,
          projectName:item.subactivity_name
        },
      };
    });
  }

  async createActivity(
    activity_type_id: number,
    user_id: number,
    chat_id: number,
    start_time: number,
    end_time: number,
    subactivity_id?: number
  ): Promise<EventResource> {
    const res = await instance.post("/calendar/add", {
      user_id: user_id,
      chat_id,
      activity_type_id,
      subactivity_id: subactivity_id ? subactivity_id : null,
      start_time,
      end_time,
    });

    return {
      activityRecord: res.data.activity_id,
      activity: activity_type_id,
      project: subactivity_id,
    };
  }

  async updateActivity(
    user_id: number,
    start_time: number,
    end_time: number,
    activity_id: number,
    activity_type_id: number,
    subactivity_id?: number
  ): Promise<void> {
    await instance.post("/calendar/edit", {
      user_id,
      start_time,
      end_time,
      activity_type_id,
      activity_id,
      subactivity_id: subactivity_id ? subactivity_id : null,
    });
    return;
  }

  async deleteActivity(user_id: number, activity_id: number): Promise<void> {
    await instance.post("/calendar/delete", {
      user_id,
      activity_id,
    });
    return;
  }

  async userIsAuth(user_id: number) {
    await instance.post("/get_user", { user_id });
  }

  async authUserForCalendar(response: any): Promise<void> {
    await instance.post("/auth", response);
  }
}
