import React, { useState, useContext } from "react";
import { Calendar, Event, momentLocalizer, View } from "react-big-calendar";
import { Wrapper } from "./calendar.styles";
import "react-big-calendar/lib/css/react-big-calendar.css";
import moment from "moment";
import "moment/locale/ru";
import { SlotInfo } from "../../common/types/calendar";
import palette from "../../common/styles/palette.json";
import Toolbar from "../toolbar";
import CalendarContext from "../../contexts/calendar-context/calendar-context";

interface CalendarComponentProps {
  onSelectSlot: (e: SlotInfo) => void;
  onSelectEvent: (event: Event) => void;
}

const CalendarComponent: React.FC<CalendarComponentProps> = ({
  onSelectSlot,
  onSelectEvent,
}) => {
  const defaultView = "week";
  const [view, setView] = useState<View>(defaultView);

  const { state } = useContext(CalendarContext);


  const setStyleEvents = ({
    resource,
    start,
    end,
  }: Event): React.HTMLAttributes<HTMLDivElement> => {
    const from = moment(start);
    const to = moment(end);
    const difference = to.diff(from, "minute", true);

    const style = {
      padding: difference > 60 ? 15 : 5,
      backgroundColor: palette[resource.activity % palette.length],
    };

    return {
      style: style,
      className: "event",
    };
  };

  const localizer = momentLocalizer(moment);

  const titleAccessor = (event: Event): string => {
    if (!event.title) {
      return "";
    }

    if (event.title.length > 9) {
      return event.title.substring(0, 7) + "...";
    }
    return event.title;
  };

  const tooltipAccessor = (event: Event): string => {
    if (!event.title) {
      return "";
    }

    if (!event.resource.projectName) {
      return event.title;
    }

    return `${event.title} (${event.resource.projectName})`;
  };

  return (
    <Wrapper view={view}>
      <Calendar
        selectable
        localizer={localizer}
        defaultView={defaultView}
        events={state.events}
        views={["day", "week"]}
        onSelectSlot={onSelectSlot}
        onSelectEvent={onSelectEvent}
        eventPropGetter={setStyleEvents}
        onView={setView}
        culture="ru"
        titleAccessor={titleAccessor}
        tooltipAccessor={tooltipAccessor}
        components={{
          toolbar: Toolbar,
        }}
      />
    </Wrapper>
  );
};

export default CalendarComponent;
