import { stringOrDate, Event } from "react-big-calendar";

export interface SlotInfo {
  start: stringOrDate;
  end: stringOrDate;
  slots: Date[] | string[];
  action: "select" | "click" | "doubleClick";
}

export interface EventResource {
  activityRecord: number;
  activity: number;
  project?: number;
  projectsName?: string;
}

export interface CalendarData {
  events: Event[];
  date: Date;
  loadingEvents: boolean;
}

export enum ActionTypesCalendar {
  ADD = "ADD",
  DELETE = "DELETE",
  UPDATE = "UPDATE",
  SET_EVENTS = "SET_EVENTS",
  SET_DATE = "SET_DATE",
  SET_LOADING_EVENTS = "SET_LOADING_EVENTS"
}

export interface IDataFromBack{
  activity_name: string;
  activity_type: number;
  end_time: number;
  id: number;
  start_time: number;
  subactivity: number | null;
  subactivity_name: string
}


