import React, { FC, useContext, useEffect, useReducer, useState } from "react";
import UserContext from "./user-context";
import userContextReducer from "./reducers";
import ServicesContext from "../services-context";
import Spinner from "../../components/spinner";
import ErrorIndicator from "../../components/error-indicator";
import { setActivities, setName, setProjects } from "./actions";
import { SpinnerWrapper } from "../../common/styles/spinner-wrapper";

// TODO: Replace from and to date to normal dates
const UserContextProvider: FC = ({ children }) => {
  const [state, dispatch] = useReducer(userContextReducer, {
    activities: [],
    projects: [],
    fromDate: new Date(),
    toDate: new Date(),
    loading: true,
    userId: 0,
    chatId: 0,
    name: "",
  });

  const { userService } = useContext(ServicesContext);
  const [error, setError] = useState(false);

  useEffect(() => {
    userService
      .getActivities(state.chatId)
      .then((activities) => dispatch(setActivities(activities)))
      .then(() => userService.getProjects(state.chatId, state.userId))
      .then((projects) => dispatch(setProjects(projects)))
      .catch(() => setError(true));

    userService
      .getUser(state.chatId, state.userId)
      .then(({ first_name, last_name }) => {
        if (last_name) {
          return  dispatch(setName(first_name + " " + last_name));
        }
        return dispatch(setName(first_name));
      })
      .catch((e) => console.log(e));
  }, [state.chatId, state.userId]);

  if (state.loading)
    return (
      <SpinnerWrapper>
        <Spinner />
      </SpinnerWrapper>
    );

  if (error) {
    return <ErrorIndicator />;
  }

  const value = { state, dispatch };

  return <UserContext.Provider value={value}>{children}</UserContext.Provider>;
};

export default UserContextProvider;
