import React, {FC, useContext, useEffect, useState} from 'react';
import ActivitiesPieGraph from "../../components/activities-pie-graph";
import ServicesContext from "../../contexts/services-context";
import {useQuery} from "react-query";
import {ActivitiesPieData} from "../../common/interfaces/user-service";
import ErrorIndicator from "../../components/error-indicator";
import {Fill} from "../../common/types/graphs";
import {GraphOuterWrapper, Header} from "../../common/styles/graph.styles";
import {ControlSection} from './activities-pie-graph-container.styles';
import Dropdown from "../../components/dropdown";
import {UserContext} from "../../contexts/user-context";
import {Themes} from "../../components/dropdown/dropdown.style";

const ActivitiesPieGraphContainer: FC = () => {
  const {state} = useContext(UserContext);
  const {userService} = useContext(ServicesContext);
  const [activityID, setActivityID] = useState<number>(0);
  const {isFetching, data, error, refetch} = useQuery<ActivitiesPieData[]>(
    ['ActivitiesPieGraphContainer', activityID],
    () => userService.getActivitiesPieData(activityID));
  const [fills, setFills] = useState<Fill[]>([]);

  const handleDropdownSelect = (value: number) => {
    setActivityID(value);
    refetch();
  }

  useEffect(() => {
    const pieStyleMap = ['lines', 'dots', 'squares', 'nothing'];

    setFills((fills) => {
      const fetchedData = data || [];
      return fetchedData.map(i => {
        return {
          id: pieStyleMap[Math.floor(Math.random() * 4)],
          match: {
            id: i.project
          }
        }
      });
    });
  }, [data]);

  if (error) {
    return (<ErrorIndicator/>);
  }
  return (
    <GraphOuterWrapper>
      <Header>
        <h3>All time activities</h3>
      </Header>
      <ControlSection>
        <Dropdown data={state.activities} value={activityID} onSelect={handleDropdownSelect}/>
      </ControlSection>
      <ActivitiesPieGraph loading={isFetching} fills={fills} data={data!}/>
    </GraphOuterWrapper>
  )
}

export default ActivitiesPieGraphContainer;