export default class UserService {
  async getCalendarData(): Promise<CalendarData[]> {
    return DUMMY_CALENDAR_DATA;
  }

  async getDayActivitiesData(): Promise<DayActivitiesData[]> {
    return DUMMY_DAY_ACTIVITIES_DATA.map((i) => {
      const projects: { [key: string]: string | number }[] = i.value.map(
        (i) => {
          const color = `hsl(${Math.floor(Math.random() * 360)}, 70%, 50%)`;
          return {
            [i.project]: i.value,
            [i.project + "Color"]: color,
          };
        }
      );
      return {
        activity: i.activity,
        ...Object.assign({}, ...projects),
      };
    });
  }

  async getActivitiesPieData(activityID: number): Promise<ActivitiesPieData[]> {
    await new Promise((resolve) =>
      setTimeout(() => {
        resolve(1);
      }, 2000)
    );
    if (activityID === 0) {
      return DUMMY_ACTIVITIES_PIE_DATA_1.map((i) => {
        const color = `hsl(${Math.floor(Math.random() * 360)}, 70%, 50%)`;
        return {
          ...i,
          color,
        };
      });
    } else if (activityID === 1) {
      return DUMMY_ACTIVITIES_PIE_DATA_2.map((i) => {
        const color = `hsl(${Math.floor(Math.random() * 360)}, 70%, 50%)`;
        return {
          ...i,
          color,
        };
      });
    } else {
      return DUMMY_ACTIVITIES_PIE_DATA_2.map((i) => {
        const color = `hsl(${Math.floor(Math.random() * 360)}, 70%, 50%)`;
        return {
          ...i,
          color,
        };
      });
    }
  }

  async getDailySleepData(): Promise<DailySleepData[]> {
    return DUMMY_DAILY_SLEEP_DATA.map((i) => {
      const color = `hsl(${Math.floor(Math.random() * 360)}, 70%, 50%)`;
      return {
        valueColor: color,
        ...i,
      };
    });
  }

  async getAverageSleepData(): Promise<AverageSleepData[]> {
    return DUMMY_AVERAGE_SLEEP_DATA.map((i) => {
      return {
        ...i,
        frequencyColor: `hsl(${Math.floor(Math.random() * 360)}, 70%, 50%)`,
      };
    });
  }

  async getProjectByActivityData(): Promise<ProjectByActivityData[]> {
    return DUMMY_PROJECT_BY_ACTIVITY_DATA.map((i) => {
      const projects: { [key: string]: string | number }[] = i.value.map(
        (i) => {
          const color = `hsl(${Math.floor(Math.random() * 360)}, 70%, 50%)`;
          return {
            [i.project]: i.value,
            [i.project + "Color"]: color,
          };
        }
      );
      return {
        date: i.date,
        ...Object.assign({}, ...projects),
      };
    });
  }

  async getActivitiesBarData(): Promise<ActivitiesBarData[]> {
    return DUMMY_ACTIVITIES_BAR_DATA.map((i) => {
      const projects: { [key: string]: string | number }[] = i.value.map(
        (i) => {
          const color = `hsl(${Math.floor(Math.random() * 360)}, 70%, 50%)`;
          return {
            [i.activity]: i.value,
            [i.activity + "Color"]: color,
          };
        }
      );
      return {
        date: i.date,
        ...Object.assign({}, ...projects),
      };
    });
  }

  async getActivities(id: number): Promise<Activity[]> {
    const res = await instance.get(`/user/activitytypes`, {
      params: { chat_id: id },
    });
    return res.data;
  }

  async getProjects(chat_id: number, user_id: number): Promise<Project[]> {
    const res = await instance.get<IProjectFromBack[]>(`/user/subactivities`, {
      params: { chat_id, user_id },
    });
    return res.data.map((item) => ({
      ...item,
      activityId: item.activity_type_id,
    }));
  }

  async getUser(chat_id: number, user_id: number): Promise<IUserData> {
    const res = await instance.get<IUserData>("/user/info", {
      params: { chat_id, user_id },
    });
    return res.data;
  }
}
