import React, { useState, useContext } from "react";
import CalendarComponent from "../../components/calendar";
import { Event } from "react-big-calendar";
import { SlotInfo } from "../../common/types/calendar";
import CalendarModalContainer from "../../containers/calendar-modal-container";
import LoadingLine from "../../components/loading-line";
import { CalendarContext } from "../../contexts/calendar-context";

const CalendarPage = () => {
  const [modalIsOpen, setIsOpen] = useState(false);
  const [from, setFrom] = useState(new Date());
  const [to, setTo] = useState(new Date());
  const [selectedEvent, setSelectedEvent] = useState<Event | null>(null);

  const {
    state: { loadingEvents },
  } = useContext(CalendarContext);

  const handlerSelectSlot = ({ end, start, action }: SlotInfo) => {
    if (
      action === "doubleClick" ||
      !(end instanceof Date) ||
      !(start instanceof Date)
    ) {
      return;
    }
    setSelectedEvent(null);
    setFrom(start);
    setTo(end);
    setIsOpen(true);
  };

  const handlerClose = () => {
    setIsOpen(false);
  };

  const handlerSelectEvent = ({ end, start, resource }: Event) => {
    setSelectedEvent({ end, start, resource });
    setFrom(start!);
    setTo(end!);
    setIsOpen(true);
  };

  return (
    <>
      {loadingEvents ? (
        <LoadingLine
          colorSlider="#D4D4D4"
          backgroundColor="transparent"
          style={{ position: "fixed", top: "65px", zIndex: 1000 }}
          dur={1.5}
        />
      ) : null}

      <CalendarModalContainer
        isOpen={modalIsOpen}
        onClose={handlerClose}
        from={from}
        to={to}
        setFrom={setFrom}
        setTo={setTo}
        selectedEvent={selectedEvent}
      />
      <CalendarComponent
        onSelectSlot={handlerSelectSlot}
        onSelectEvent={handlerSelectEvent}
      />
    </>
  );
};
export default CalendarPage;
