import React, {FC, useContext, useEffect, useState} from 'react'
import GraphInnerWrapper from "../../common/styles/graph-inner-wrapper";
import {ResponsivePie} from "@nivo/pie";
import {SvgDefsAndFill} from '@nivo/core';
import ServicesContext from "../../contexts/services-context";
import {useQuery} from "react-query";
import {ActivitiesPieData} from "../../common/interfaces/user-service";
import Spinner from "../spinner";
import ErrorIndicator from "../error-indicator";
import {Fill} from "../../common/types/graphs";


interface Props {
  data: ActivitiesPieData[];
  fills: Fill[],
  loading: boolean
}

const ActivitiesPieGraph: FC<Props> = ({data, fills, loading}) => {
  if (loading) return <Spinner/>
  return (
    <GraphInnerWrapper>
      <ResponsivePie
        id={'project'}
        data={data}
        margin={{ top: 40, right: 80, bottom: 80, left: 80 }}
        innerRadius={0.5}
        padAngle={0.7}
        cornerRadius={3}
        activeOuterRadiusOffset={8}
        borderWidth={1}
        borderColor={{ from: 'color', modifiers: [ [ 'darker', 0.2 ] ] }}
        arcLinkLabelsSkipAngle={10}
        arcLinkLabelsTextColor="#333333"
        arcLinkLabelsThickness={2}
        arcLinkLabelsColor={{ from: 'color' }}
        arcLabelsSkipAngle={10}
        arcLabelsTextColor={{ from: 'color', modifiers: [ [ 'darker', 2 ] ] }}
        defs={[
          {
            id: 'dots',
          type: 'patternDots',
          background: 'inherit',
          color: 'rgba(255, 255, 255, 0.3)',
          size: 4,
          padding: 1,
          stagger: true
        },
        {
          id: 'lines',
          type: 'patternLines',
          background: 'inherit',
            color: 'rgba(255, 255, 255, 0.3)',
            rotation: -45,
            lineWidth: 6,
            spacing: 10
          }
        ]}
        fill={fills}
        legends={[
          {
            anchor: 'bottom',
            direction: 'row',
            justify: false,
            translateX: 0,
            translateY: 56,
            itemsSpacing: 0,
            itemWidth: 100,
            itemHeight: 18,
            itemTextColor: '#999',
            itemDirection: 'left-to-right',
            itemOpacity: 1,
            symbolSize: 18,
            symbolShape: 'circle',
            effects: [
              {
                on: 'hover',
                style: {
                  itemTextColor: '#000'
                }
              }
            ]
          }
        ]}
      />

    </GraphInnerWrapper>

  )
}

export default ActivitiesPieGraph;