import { ActionTypesUser, UserData } from "../../common/types/user";
import { Action } from "./actions";

const userContextReducer = (state: UserData, action: Action): UserData => {
  switch (action.type) {
    case ActionTypesUser.SET_ACTIVITIES:
      return {
        ...state,
        activities: action.payload,
        loading: false,
      };
    case ActionTypesUser.SET_FROM_DATE:
      return {
        ...state,
        fromDate: action.payload,
      };
    case ActionTypesUser.SET_TO_DATE:
      return {
        ...state,
        toDate: action.payload,
      };
    case ActionTypesUser.SET_PROJECTS:
      return {
        ...state,
        projects: action.payload,
      };
    case ActionTypesUser.SET_CHAT_AND_USER_ID:
      return {
        ...state,
        chatId: action.payload.chatId,
        userId: action.payload.userId,
      };
    case ActionTypesUser.SET_NAME:
      return { ...state, name: action.payload };
    default:
      return state;
  }
};

export default userContextReducer;
