import styled from "styled-components";

export const Wrapper = styled.div`
  display: inline-flex;
  font-size: 40px;
  background: #ffffff;
  box-shadow: 0px 8px 20px rgba(14, 30, 109, 0.1);
  justify-content: center;
  align-items: center;
  padding: 5px;
  border-radius: 50px;

  span {
    font-size: 25px;
    cursor: pointer;
    text-transform: uppercase;
  }

  svg {
    cursor: pointer;
  }
  
`;