import { ActionTypesCalendar } from "../../common/types/calendar";
import { Event } from "react-big-calendar";

export type Action =
  | { type: ActionTypesCalendar.ADD; payload: Event }
  | { type: ActionTypesCalendar.DELETE; payload: Event }
  | { type: ActionTypesCalendar.UPDATE; payload: Event }
  | { type: ActionTypesCalendar.SET_EVENTS; payload: Event[] }
  | { type: ActionTypesCalendar.SET_DATE; payload: Date }
  | { type: ActionTypesCalendar.SET_LOADING_EVENTS, payload: boolean };

const setEvents = (events: Event[]): Action => {
  return { type: ActionTypesCalendar.SET_EVENTS, payload: events };
};

const addEvent = (event: Event): Action => {
  return { type: ActionTypesCalendar.ADD, payload: event };
};

const updateEvents = (event: Event): Action => {
  return { type: ActionTypesCalendar.UPDATE, payload: event };
};

const deleteEvent = (event: Event): Action => {
  return { type: ActionTypesCalendar.DELETE, payload: event };
};

const setDate = (date: Date): Action => {
  return { type: ActionTypesCalendar.SET_DATE, payload: date };
};

const setLoadingEvents = (payload: boolean): Action => {
  return { type: ActionTypesCalendar.SET_LOADING_EVENTS, payload };
};

export { setEvents, addEvent, updateEvents, deleteEvent, setDate,  setLoadingEvents};
