import React, { useEffect } from "react";
import Modal from "react-modal";
import { BsX } from "react-icons/bs";
import Dropdown from "../dropdown";
import { Activity, Project } from "../../common/types/user";
import Spinner from "../spinner";
import ErrorIndicator from "../error-indicator";
import Time from "../time";
import { Wrapper } from "./add-events-modal.styles";
import { customStyles } from "./custom-styles";

interface PropsAddEventsModal {
  isSending: boolean; // sending use when save data
  isOpen: boolean;
  data: { activities: Activity[] | undefined; projects: Project[] | undefined };
  handlerAfterClose: () => void;
  onClose: () => void;
  handlerSubmit: (e: React.SyntheticEvent) => void;
  activityId: number | "";
  projectId: number | "";
  handlerSetActivity: (id: number) => void;
  projectsCurrentActivity: Project[];
  setProjectId: (id: number) => void;
  from: Date;
  to: Date;
  isCorrectData: boolean;
  setFrom: (from: Date) => void;
  setTo: (to: Date) => void;
  handlerAfterOpen: () => void;
  errorDelete: boolean;
  isSelectedEvent: boolean;
  onDelete: () => Promise<void>;
}

const AddEventsModal: React.FC<PropsAddEventsModal> = ({
  isSending,
  isOpen,
  data,
  handlerAfterClose,
  handlerAfterOpen,
  onClose,
  handlerSubmit,
  activityId,
  projectId,
  handlerSetActivity,
  projectsCurrentActivity,
  setProjectId,
  from,
  to,
  setFrom,
  setTo,
  isCorrectData,
  errorDelete,
  isSelectedEvent,
  onDelete,
}) => {
  useEffect(() => {
    Modal.setAppElement("body");
  }, []);

  if (isSending) {
    return (
      <Modal isOpen={isOpen} style={customStyles}>
        <Wrapper
          isSelectedEvent={isSelectedEvent}
          isCorrectData={isCorrectData}
        >
          <Spinner />
        </Wrapper>
      </Modal>
    );
  }

  if (errorDelete) {
    return <ErrorIndicator />;
  }

  if (!data.activities || !data.projects) {
    return <p>You have not used this bot yet..</p>;
  }

  return (
    <Modal
      isOpen={isOpen}
      onAfterClose={handlerAfterClose}
      onAfterOpen={handlerAfterOpen}
      style={customStyles}
    >
      <Wrapper isSelectedEvent={isSelectedEvent} isCorrectData={isCorrectData}>
        <div className="modal-header">
          <BsX onClick={onClose} size="25px" style={{ cursor: "pointer" }} />
        </div>
        <form className="modal-body" onSubmit={handlerSubmit}>
          <div className="container">
            <Dropdown
              value={activityId}
              data={data.activities}
              onSelect={handlerSetActivity}
              placeholder="Activity"
            />
          </div>
          <div className="container">
            <Dropdown
              value={projectId}
              data={projectsCurrentActivity}
              onSelect={setProjectId}
              placeholder="Projects"
            />
          </div>
          <div className="container">
            <div className="time-wrapper">
              <span>От:</span>
              <div className="time">
                <Time time={from} setTime={setFrom} />
              </div>
            </div>
          </div>
          <div className="container">
            <div className="time-wrapper">
              <span>До:</span>
              <div className="time">
                <Time time={to} setTime={setTo} />
              </div>
            </div>
          </div>
          <div className="container">
            <button type="submit" className="btn">
              Save
            </button>
            {isSelectedEvent ? (
              <button
                type="submit"
                className="btn btn-delete"
                onClick={onDelete}
              >
                Delete
              </button>
            ) : null}
          </div>
        </form>
      </Wrapper>
    </Modal>
  );
};

export default AddEventsModal;
