import React, { useContext } from "react";
import { Wrapper } from "./calendar-navigation.styles";
import { IoIosArrowBack, IoIosArrowForward } from "react-icons/io";
import { NavigateAction, View } from "react-big-calendar";
import { CalendarContext } from "../../contexts/calendar-context";
import ServicesContext from "../../contexts/services-context";
import moment, { Moment } from "moment";
import { setEvents, setDate, setLoadingEvents } from "../../contexts/calendar-context";
import { UserContext } from "../../contexts/user-context";

interface CalendarNavigationProps {
  onNavigate: (navigate: NavigateAction, date?: Date) => void;
  view: View;
}

const CalendarNavigation: React.FC<CalendarNavigationProps> = ({
  onNavigate,
  view,
}) => {
  const {
    state: { userId, chatId },
  } = useContext(UserContext);

  const {
    state: { date },
    dispatch,
  } = useContext(CalendarContext);

  const { calendarService } = useContext(ServicesContext);

  const newDate = (operator: "+" | "-"): Moment => {
    if (view === "day") {
      return operator === "+"
        ? moment(date).add(1, "day")
        : moment(date).add(-1, "day");
    }

    return operator === "+"
      ? moment(date).add(7, "day")
      : moment(date).add(-7, "day");
  };

  const checkDate = async (currentDate: Moment) => {
    const mondayOld = moment(date).startOf("week");
    const saturdayOld = moment(date).endOf("week");

    if (!currentDate.isBetween(mondayOld, saturdayOld)) {
      const mondayCurrent = currentDate.startOf("week").toDate();
      const saturdayCurrent = currentDate.endOf("week").toDate();

      dispatch(setLoadingEvents(true));
      const events = await calendarService.getEventData(
        userId,
        chatId,
        mondayCurrent.getTime(),
        saturdayCurrent.getTime()
      );

      dispatch(setEvents(events));
      dispatch(setLoadingEvents(false));
    }

    dispatch(setDate(currentDate.toDate()));
  };

  const prev = () => {
    onNavigate("PREV");
    const currentDate = newDate("-");
    return checkDate(currentDate);
  };

  const today = async () => {
    onNavigate("TODAY");
    return checkDate(moment(new Date()));
  };

  const next = async () => {
    onNavigate("NEXT");
    const currentDate = newDate("+");
    return checkDate(currentDate);
  };

  return (
    <Wrapper>
      <IoIosArrowBack onClick={prev} />
      <span onClick={today}>Today</span>
      <IoIosArrowForward onClick={next} />
    </Wrapper>
  );
};
export default CalendarNavigation;
