import React, { useState, useEffect } from "react";
import { Button, Wrapper } from "./switch.styles";
import { FaCalendarDay, FaCalendarWeek } from "react-icons/fa";
import { View } from "react-big-calendar";

interface SwitchProps {
  onView: (view: View) => void;
  view: View;
}

const Switch: React.FC<SwitchProps> = ({ onView, view }) => {
  const [week, setWeek] = useState(true);
  const [day, setDay] = useState(false);

  useEffect(() => {
    if(view === "week") {
      setDay(false);
      setWeek(true);
      return;
    }

    if(view === "day") {
      setDay(true);
      setWeek(false);
      return;
    }
  }, [view]);

  return (
    <Wrapper>
      <Button
        selected={week}
        onClick={() => {
          onView("week");
        }}
      >
        Week <FaCalendarWeek />
      </Button>
      <Button
        selected={day}
        onClick={() => {
          onView("day");
        }}
      >
        Day <FaCalendarDay />
      </Button>
    </Wrapper>
  );
};

export default Switch;
