import React, {FC} from 'react';
import GraphInnerWrapper from "../../common/styles/graph-inner-wrapper";
import {ResponsiveBar} from "@nivo/bar";
import {AverageSleepData} from "../../common/interfaces/user-service";
import Spinner from "../spinner";

interface Props {
  data: AverageSleepData[],
  loading: boolean
}

const AverageSleepGraph: FC<Props> = ({data, loading}) => {
  if (loading) return <Spinner/>
  return (
    <GraphInnerWrapper>
      <ResponsiveBar
        data={data}
        keys={['frequency']}
        indexBy={'duration'}
        margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
        padding={0}
        layout="vertical"
        valueScale={{ type: 'linear' }}
        indexScale={{ type: 'band', round: true }}
        colors={{ scheme: 'nivo' }}
        defs={[
          {
            id: 'dots',
            type: 'patternDots',
            background: 'inherit',
            color: '#38bcb2',
            size: 4,
            padding: 1,
            stagger: true
          },
          {
            id: 'lines',
            type: 'patternLines',
            background: 'inherit',
            color: '#eed312',
            rotation: -45,
            lineWidth: 6,
            spacing: 10
          }
        ]}
        fill={[
          {
            id: 'dots',
            match: {
              id: 'duration'
            }
          }
        ]}
        borderColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
        axisTop={null}
        axisRight={null}
        axisBottom={{
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: 'Sleep duration',
          legendPosition: 'middle',
          legendOffset: 32
        }}
        axisLeft={{
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: 'Frequency',
          legendPosition: 'middle',
          legendOffset: -40
        }}
        labelSkipWidth={12}
        labelSkipHeight={12}
        labelTextColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
        animate={true}
        motionStiffness={90}
        motionDamping={15}
      />
    </GraphInnerWrapper>
  )
}

export default AverageSleepGraph;