import React, {FC} from 'react';
import GraphInnerWrapper from "../../common/styles/graph-inner-wrapper";
import {ProjectByActivityData} from "../../common/interfaces/user-service";
import {ResponsiveBar} from "@nivo/bar";
import {Fill} from "../../common/types/graphs";
import Spinner from "../spinner";

interface Props {
  data: ProjectByActivityData[],
  fills: Fill[],
  keys: string[],
  indexBy: string,
  loading: boolean
}

const ProjectsByActivityGraph: FC<Props> = ({data, fills, keys, indexBy, loading}) => {
  if (loading) return <Spinner/>
  return (
    <GraphInnerWrapper>
      <ResponsiveBar
        data={data}
        keys={keys}
        indexBy={indexBy}
        margin={{top: 50, right: 130, bottom: 50, left: 60}}
        padding={0.3}
        layout="vertical"
        valueScale={{type: 'linear'}}
        indexScale={{type: 'band', round: true}}
        colors={{scheme: 'nivo'}}
        defs={[
          {
            id: 'dots',
            type: 'patternDots',
            background: 'inherit',
            color: '#38bcb2',
            size: 4,
            padding: 1,
            stagger: true
          },
          {
            id: 'lines',
            type: 'patternLines',
            background: 'inherit',
            color: '#eed312',
            rotation: -45,
            lineWidth: 6,
            spacing: 10
          }
        ]}
        fill={fills}
        borderColor={{from: 'color', modifiers: [['darker', 1.6]]}}
        axisTop={null}
        axisRight={null}
        axisBottom={{
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: 'Date',
          legendPosition: 'middle',
          legendOffset: 32
        }}
        axisLeft={{
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: 'Hours',
          legendPosition: 'middle',
          legendOffset: -40
        }}
        labelSkipWidth={12}
        labelSkipHeight={12}
        labelTextColor={{from: 'color', modifiers: [['darker', 1.6]]}}
        legends={[
          {
            dataFrom: 'keys',
            anchor: 'bottom-right',
            direction: 'column',
            justify: false,
            translateX: 120,
            translateY: 0,
            itemsSpacing: 2,
            itemWidth: 100,
            itemHeight: 20,
            itemDirection: 'left-to-right',
            itemOpacity: 0.85,
            symbolSize: 20,
            effects: [
              {
                on: 'hover',
                style: {
                  itemOpacity: 1
                }
              }
            ]
          }
        ]}
        animate={true}
        motionStiffness={90}
        motionDamping={15}
      />
    </GraphInnerWrapper>
  )
}

export default ProjectsByActivityGraph;