import React, {useCallback, useContext, useEffect, useState} from "react";
import ServicesContext from "../../contexts/services-context";
import {useQuery} from "react-query";
import {Fill} from "../../common/types/graphs";
import Spinner from "../../components/spinner";
import ErrorIndicator from "../../components/error-indicator";
import {DailySleepData} from "../../common/interfaces/user-service";
import DailySleepGraph from "../../components/daily-sleep-graph";
import {GraphOuterWrapper} from "../../common/styles/graph.styles";

const DailySleepGraphContainer = () => {
  const {userService} = useContext(ServicesContext);
  const {isFetching, data, error} = useQuery<DailySleepData[]>('DailySleep', userService.getDailySleepData);
  const [fills, setFills] = useState<Fill[]>([]);

  const getKeys = useCallback(
    (): string[] => {
      const keys: string[] = [];
      if (data) {
        data.map(i => {
          for (const [key, value] of Object.entries(i)) {
            if (
              key !== 'date' &&
              keys.findIndex(i => i === key) === -1 &&
              !key.match(new RegExp('Color', 'ig'))) {
              keys.push(key);
            }
          }
        });
      }
      else {
        return []
      }
      return keys;
    }, [data]);

  useEffect(() => {
    setFills((fills) => {
      return getKeys().map(i => {
        return {
          id: 'dots',
          match: {
            id: i
          }
        }
      });
    });
  }, [data]);


  if (error) {
    return (<ErrorIndicator/>);
  }

  if (!data) {
    return (<p>You have not used this bot yet..</p>);
  }

  return (
    <GraphOuterWrapper>
      <DailySleepGraph loading={isFetching} data={data} fills={fills} indexBy={'date'} keys={getKeys()}/>
    </GraphOuterWrapper>
  )
}

export default DailySleepGraphContainer;