import React, { useEffect, useContext } from "react";
import { useParams } from "react-router-dom";
import { setChatAndUserId, UserContext } from "../../contexts/user-context";

const ParamsContainer: React.FC = ({ children }) => {
  const { chat_id, user_id } = useParams<{
    chat_id: string;
    user_id: string;
  }>();

  const { dispatch } = useContext(UserContext);

  useEffect(() => {
    dispatch(setChatAndUserId(+chat_id, +user_id));
  }, []);

  return <>{children}</>;
};

export default ParamsContainer;
