export interface Activity {
  id: number;
  name: string;
}

export interface Project extends Activity {
  activityId: number;
}

export interface IProjectFromBack extends Activity {
  activity_type_id: number;
}

export interface UserData {
  activities: Activity[];
  projects: Project[];
  fromDate: Date;
  toDate: Date;
  loading: boolean;
  chatId: number;
  userId: number;
  name: string;
}

export enum ActionTypesUser {
  SET_ACTIVITIES = "setActivities",
  SET_PROJECTS = "setProjects",
  SET_TO_DATE = "setToDate",
  SET_FROM_DATE = "setFromDate",
  SET_CHAT_AND_USER_ID = "setChatAndUserId",
  SET_NAME = "setName",
}

export interface IUserData {
  first_name: string;
  last_name?: string;
  id: number;
  language_code: string;
  username: string;
}
