import styled from "styled-components";

export const Wrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding: 15px;

  @media (max-width: 520px) {
    flex-direction: column;
    height: 200px;

    & div:nth-child(1) {
      order: 1;
    }
    
    & div:nth-child(2) {
      order: 3;
      
    }
  }
`;
