import styled from "styled-components";

export const Wrapper = styled.div`
  z-index: 1000;
  position: fixed;
  margin-top: 65px;
  background: linear-gradient(
    90deg,
    rgba(230, 69, 88, 1) 34%,
    rgba(220, 90, 51, 0.8382703423166141) 72%
  );

  width: 100%;
  height: 70px;

  .sub-header-content {
    margin-right: 25px;
    margin-left: 25px;
    height: 100%;
    border-top: 2px solid rgba(255, 255, 255, 0.5);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 10px;
    padding-right: 10px;
  }

  .from-to {
    display: flex;
    align-items: center;
    height: 100%;

    .from {
      margin-right: 15px;
    }

    .react-date-picker__wrapper {
      border: 3px solid #fff;
      border-radius: 5px;
    }

    .react-date-picker__inputGroup__input {
      color: #fff;
    }
    .react-date-picker__inputGroup__input::placeholder  {
      color: #fff;
    }

    .react-date-picker__inputGroup__divider {
      color: #fff;
    }

    svg {
      stroke: white!important;
    }
    
  }

  .calendar {
    display: flex;
    align-items: center;
    height: 100%;
  }

  @media (max-width: 525px) {
    & {
      height: 130px;
    }

    .sub-header-content {
      flex-direction: column;
      align-items: unset;
      padding-left: 0px;
      padding-right: 0px;
    }

    .from-to {
      margin-top: 5px;
      width: 100%;
      justify-content: center;

      .from {
        margin-right: 25px;
      }
    }

    .calendar {
      margin-top: 10px;
      margin-bottom: 5px;
    }
  }

  @media (max-width: 350px) {
    & {
      height: 140px;
    }

    .from-to {
      flex-direction: column;

      .from {
        margin-right: 0px;
        margin-bottom: 10px;
      }
    }
  }
`;

export const Button = styled.button`
  border: none;
  background: none;
  padding: 10px;
  border: 3px solid #fff;
  border-radius: 15px;
  color: #fff;
  cursor: pointer;
`;
