import React, {useContext} from 'react';
import AverageSleepGraph from "../../components/average-sleep-graph";
import ServicesContext from "../../contexts/services-context";
import {useQuery} from "react-query";
import {AverageSleepData} from "../../common/interfaces/user-service";
import Spinner from "../../components/spinner";
import ErrorIndicator from "../../components/error-indicator";
import {GraphOuterWrapper} from "../../common/styles/graph.styles";

const AverageSleepGraphContainer = () => {
  const {userService} = useContext(ServicesContext);
  const {isFetching, data, error} = useQuery<AverageSleepData[]>('AverageSleepData', userService.getAverageSleepData);

  if (error) {
    return (<ErrorIndicator/>);
  }

  if (!data) {
    return (<p>You have not used this bot yet..</p>);
  }

  return (
    <GraphOuterWrapper>
      <AverageSleepGraph loading={isFetching} data={data}/>
    </GraphOuterWrapper>
  )
}

export default AverageSleepGraphContainer;