import React from "react";

interface IProps {
  height?: number;
  backgroundColor?: string;
  colorSlider?: string;
  dur?: number;
  style?: React.CSSProperties;
}

const LoadingLine: React.FC<IProps> = ({
  height = 2,
  backgroundColor = "green",
  dur = 2,
  colorSlider = "#feac5e",
  style,
}) => {
  return (
    <svg
      viewBox={`0 0 700 ${height}`}
      style={{ backgroundColor: backgroundColor, ...style }}
    >
      <rect x="0" y="0" width="35" height={height} fill={colorSlider}>
        <animate
          attributeName="x"
          from="0"
          to="700"
          dur={`${dur}s`}
          repeatCount="indefinite"
        />
      </rect>
    </svg>
  );
};

export default LoadingLine;
