import React, {FC, useContext} from 'react';
import CalendarGraph from "../../components/calendar-graph";
import ServicesContext from "../../contexts/services-context";
import {useQuery} from "react-query";
import {CalendarData} from "../../common/interfaces/user-service";
import Spinner from "../../components/spinner";
import ErrorIndicator from "../../components/error-indicator";
import {GraphOuterWrapper} from "../../common/styles/graph.styles";

const CalendarGraphContainer: FC = () => {
  const {userService} = useContext(ServicesContext);
  const {error, data, isFetching} = useQuery<CalendarData[]>('calendar-graph-data', userService.getCalendarData);

  if (error) {
    return (<ErrorIndicator/>);
  }

  if (!data) {
    return (<p>You have not used this bot yet..</p>);
  }

  return (
    <GraphOuterWrapper>
      <CalendarGraph loading={isFetching} data={data}/>
    </GraphOuterWrapper>
  )
}

export default CalendarGraphContainer;